.top_nav {
  
  header {
    background: #FFF;
    height: 55px;
    width: 100%;
    padding: 0 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    // & > div {
    //   width: 75px;
    //   background: red;
    // }
    img {
      max-width: 30px;
    }
  }
}

nav.top_nav {
  height: 45px;
  background: #F6F6F6;
  box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.10);
  display: flex;
  position: relative;
  
  align-items: center;
  justify-content: center;
  
  .title {
    height: 100%;
    background: none;
    
    display: flex;
    flex: auto;
    align-items: center;
    
    h4 {
      padding: 0;
      margin: 0;
      align-self: center;
      font-weight: 300;
      font-size: 16px;
      padding-left: 25px;
    }
  }

  .nav_button {
    width: 150px;
    height: 100%;
    background: none;
    flex-shrink: 0;
    border-bottom: 1px solid #ED1E79;
    display: flex;
    align-items: center;
    cursor: pointer;

    
    h4 {
      padding: 0;
      margin: 0;
      align-self: center;
      color: #ED1E79;
      font-weight: 300;
      font-family: 'Open Sans Condensed', sans-serif;
      padding-left: 12px; 
    }

    i {
      color: #ED1E79;
      align-self: center;
    }
    
  }
}

